import Api from '../../api/user';
	
 export default {
        data() {
            return {
				loginForm: {  
				    workNo: "05370",  
				    password: "123456",  
				}, 
            }
        },
		onLoad() {
			if(uni.getStorageSync('token') != null){
				this.direct(uni.getStorageSync('role'))
			}
		},
        methods: {
			makeLogin(e){
				wx.login({
					timeout:5000,
					success:(res)=>{
						let data = {
							workNo: e.detail.value.workNo,
							password: e.detail.value.password,
							code: res.code
						}
						Api.login(data)
						.then(res => {
							uni.hideLoading()
							if(res.code==200){
								if(res.data.role=='2'){
									uni.showToast({
										icon:'success',
										mask:true,
										title: '管理员无权登录',
										duration: 2000,
									})
								}else{
									uni.setStorageSync('token', res.data.token);
									uni.setStorageSync('role', res.data.role);
									this.direct(res.data.role)
								}
							}
						})
					}
				})
			},
            login: function(e) {
				uni.showLoading({
				    title: '登录中',	
				})
				if(!uni.getStorageSync('token')){
					this.makeLogin(e)
				}else{
					uni.hideLoading()
					wx.checkSession({
						success:()=>{
							console.log('登录态有效')
							this.direct(uni.getStorageSync('role'))
						},
						fail: () => {
							console.log('登录态失效')
							this.makeLogin(e)
						}
					})
				}
            },
			direct(data){

				if(data== '0'){
					uni.showToast({
						icon:'success',
						mask:true,
						title: '登录成功',
						duration: 1000,
						success:function(){
							uni.switchTab({
								url: '/pages/his_wo/his_wo'
							});
						}
					})
				}else if(data=='1'||data=='3'){
					uni.showToast({
						icon:'success',
						mask:true,
						title: '工程师',
						duration: 1000,
						success:function(){
							uni.reLaunch({
								url: '/pages/engineer/engineer'
							})
						}
					})
				}
			}
        }
    }
